<template>
  <div class="reset-psw-view view-background">
    <header-view :title="title" :fixed="fixed" :routerName="routerName"></header-view>
    <section class="reset-psw-input">
      <div class="psw-input"><label>新密码</label><input type="password" v-model="password" v-bind:value="password"></div>
    </section>
    <div class="input-help">英文字母或数字、限6-32字符</div>
    <div class="setting-psw-btn" @click="touser">确定</div>
  </div>
</template>

<script>
  import {postReset} from '../../service/getData'
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        title: '设置密码',
        routerName: '',
        password: '',
        fixed: false,
        phone: '',
        vcode: ''
      }
    },
    created: function () {
      if (this.$route.params.routerName1) {
        localStorage.setItem('routerName1', this.$route.params.routerName1)
      }
      this.routerName = this.$route.params.routerName1 === undefined ? localStorage.routerName1 : this.$route.params.routerName1
      if (this.$route.params.phone) {
        this.phone = this.$route.params.phone
      }
      if (this.$route.params.vcode) {
        this.vcode = this.$route.params.vcode
      }
    },
    methods: {
      touser: function () {
        var _this = this
        if(_this.password == '' || _this.password == ' '){
          _this.$toast('新密码不能为空')
          return
        }
        postReset(_this.phone, _this.password, _this.vcode).then(function (rep) {
          if(rep.data.code == '1004'){
            _this.$toast('验证码已过期')
            return
          }else if(rep.data.code == '1005'){
            _this.$toast('验证码不正确')
            return
          }else if(rep.data.code == '1007'){
            _this.$toast('验证码有误')
            return
          }else if(rep.data.code =='1'){
            //成功后页面跳转
            _this.$toast('设置密码成功！')
            _this.$router.push({
              name: 'login'
            })
          }else{
            _this.$toast('操作失败，服务器返回数据异常！')
          }
        }).catch(function () {
            _this.$toast('操作失败，连接服务器异常！')
        })
      }
    }
  }
</script>

<style>
/* 设置密码页面*/

.reset-psw-view {
  height: 100%;
}

.reset-psw-input {
  background-color: #FFFFFF;
  margin-top: .2rem;
  padding-left: .15rem;
}

.psw-input {
  height: .55rem;
  line-height: .55rem;
}

.psw-input label {
  color: #333;
  margin-right: .1rem;
}

.psw-input input {
  height: 100%;
}
</style>


